.container{
    display: flex;
    justify-content: center;
}
.appDiv{
    border: 7px solid lightcoral;
    display: flex;
    flex-direction: column;
    padding: 0 15px 15px 15px;
    margin-top: 10vh;
    background-color: lavender;
}

#jerry{
    text-align: end;
    padding-right: 10%;
}
.drumDiv{
    display: flex;
    padding: 0 15px 15px 15px;
}
.buttonDiv{
    width: 300px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}
button{
    font-family: 'Russo One', sans-serif;
    border: none;
    -webkit-box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.75);
    box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.75);
}
button:active{
    outline: none;
    background-color: skyblue;
    transform: scale(0.9);
}
button:focus{
    outline: none;
}
.drumButton{
    width: 30%;
    height: 70px;
    background-color: thistle;
    margin-top: 10px;
    border-radius: 7px;
}
.optDiv{
    width: 300px;
    padding: 15px 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

#powerDiv,#bankDiv{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    margin-left: 40%;}
.optButtons,.bankButtons{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 60px;
    height: 20px;
    background-color: black;
    border-radius: 5px;
}
.powerButton,.bankButton{
    width: 40%;
    height: 60%;
    border-radius: 5px;
}
input{
    width: 75%;
    margin-left: 13%;
    
}

.slider {
    -webkit-appearance: none;
    height: 15px;
    border-radius: 5px;  
    background: white;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: rgb(213, 118, 236);
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
  }

#state{
    padding: 20px 0;
    width: 50%;
    background-color: thistle;
    margin-left: 25%;
    text-align: center;
    border-radius: 10px;
}

@media(orientation: portrait){
    .drumDiv{
        flex-direction: column;
    }
    .appDiv{
        width: 70vw;
    }
    .buttonDiv,.optDiv{
        width: 100%;
    }
}